<script>
// @ is an alias to /src
import Flipbook from "flipbook-vue";
export default {
  name: "Home",
  components: { Flipbook },
  data: function() {
    return {
      end: false,
      use: [],
      useAnswers: ["food", "medicine", "shelter", "trade", "tools"],
      hear: [],
      hearAnswers: ["people", "boats", "birds", "animals", "water"],
      past: [],
      pastAnswers: ["people", "birds", "animals", "water"],
      plants: [],
      plantAnswers: ["banskia", "lomandra", "westringia", "carpobrotus"],
      repurposed: [],
      repurposeAnswers: ["bricks", "pavers", "poles", "stones"],
      people: [],
      peopleAnswers: ["exercise", "picnic", "events", "scooting", "friends"],
      bats: [],
      batsAnswers: ["dark", "food", "quiet", "safe"],
      garden: [],
      gardenAnswers: ["compost", "beehive", "veg", "beds", "worms", "fruits", "greens", "flowers"],
      environment: [],
      environmentAnswers: ["solar", "conditioners", "paint", "curtains", "glazing", "fans"],
      questions: [[null, null], 
                  [null, null], 
                  [null, null, null, null, null],
                  [null], 
                  [null], 
                  [null, null], 
                  [null, null],
                  [null, null, null, null]],
      pages: [
        null,
        "0_corner.png",
        "1_corner.png",
        "2_corner.png",
        "3_corner.png",
        "Artboard – 4.png",
        "5_corner.png",
        "Artboard – 6.png",
        "7_corner.png",
        "8_corner.png",
        "9_corner.png",
        "10_corner.png",
        "11_corner.png",
        "12_corner.png",
        "13_corner.png",
        "14.png"
      ],
      ui: true,
      grabbing: false,
      page: 0,
      flipbook: null,
      score: 0,
      max: 0
    };
  },
  methods: {
    getScore() {
      let total = 0;
      let max = 0;
      for(let question of this.questions) {
        for(let answer of question) {
          ++max;
          if(answer == true) {
            ++total;
          }
        }
      }
      for(let checkbox of this.useAnswers) {
        ++max
        if(this.use.includes(checkbox)) {
          ++total;
        }
      }
      for(let checkbox of this.hearAnswers) {
        ++max
        if(this.hear.includes(checkbox)) {
          ++total;
        }
      }
      for(let checkbox of this.pastAnswers) {
        ++max
        if(this.past.includes(checkbox)) {
          ++total;
        }
      }
      for(let checkbox of this.batsAnswers) {
        ++max
        if(this.bats.includes(checkbox)) {
          ++total;
        }
      }
      for(let checkbox of this.plantAnswers) {
        ++max
        if(this.plants.includes(checkbox)) {
          ++total;
        }
      }
      for(let checkbox of this.repurposeAnswers) {
        ++max
        if(this.repurposed.includes(checkbox)) {
          ++total;
        }
      }
      for(let checkbox of this.peopleAnswers) {
        ++max
        if(this.people.includes(checkbox)) {
          ++total;
        }
      }
      for(let checkbox of this.gardenAnswers) {
        ++max
        if(this.garden.includes(checkbox)) {
          ++total;
        }
      }
      for(let checkbox of this.environmentAnswers) {
        ++max
        if(this.environment.includes(checkbox)) {
          ++total;
        }
      }
      this.score = total;
      this.max = max;
    },
    onFlipLeftEnd(page) {
      this.page = page;
      this.ui = true;
    },
    hideUI() {
      this.ui = false;
    },
    onFlipRightEnd(page) {
      this.page = page;
      if(this.page == 15) {
        this.getScore();
        this.$bvModal.show("modal-1");
        this.end = true;
      }
      this.ui = true;
    },
    checkGrab() {
      this.grabbing = this.flipbook.cursor == "grabbing";
    }
  },
  mounted() {
    window.addEventListener("mousemove", this.checkGrab);
    window.addEventListener("touchend", this.checkGrab);
    window.addEventListener("touchmove", this.checkGrab);
    this.flipbook = this.$refs["flipbook"];
  }
};
</script>

